import React, { useState } from "react";

const App = () => {
    const [values, setValues] = useState({ dr: 0, diceDR: "" });

    const onChange = (event) => {
        event.persist();
        setValues((values) => ({
            ...values,
            [event.target.name]: event.target.value,
        }));
    };

    const calcDRDice = () => {
        let floatDice = values.dr / 3.5,
            fullDice = Math.floor(floatDice),
            diceExtra =
                floatDice % 1 > 0.75
                    ? 3
                    : floatDice % 1 > 0.5
                    ? 2
                    : floatDice % 1 > 0.25
                    ? 1
                    : 0;
        setValues((values) => ({
            ...values,
            diceDR: `${fullDice}d+${diceExtra}`,
        }));
    };

    return (
        <div>
            <form>
                <input
                    type="number"
                    name="dr"
                    value={values.dr}
                    onChange={onChange}
                />
                <input type="button" onClick={calcDRDice} />
                {values.diceDR}
            </form>
        </div>
    );
};

export default App;
